import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../components/subject/SubjectSummary'
import { SubjectContent } from '../../components/subject/SubjectContent'
import { SubjectTitle } from '../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../components/subject/SubjectIdentifier'
import { Subject } from '../../components/subject/Subject'

export function Material () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>alchemy-material</SubjectIdentifier>
      <SubjectKeyword>Alchimie</SubjectKeyword>
      <SubjectTitle>Définitions</SubjectTitle>
      <SubjectSummary>

      </SubjectSummary>
      <SubjectContent>
        <p>
          L'alchimie est un domaine qui requiert une certaine rigueur pour
          permettre à ses pratiquants, les alchimistes, de communiquer
          fidèlement entre eux, et surtout pour eux-même, le résultat de leurs
          travaux et recherches. Cette première partie propose de faire un tour
          des définitions nécéssaires et suffisantes pour comprendre le domaine
          passionant qu'est l'alchimie.
        </p>

        <p>
          <em>Transformation.</em> On appelle transformation tout procédé
          permettant d'obtenir une ou plusieure matières dites matières
          produites en consomant une ou plusieurs matières dites matières
          consommées. L'infusion, la décoction, la macération, le séchage, la
          dilution, la concentration, le broyage et la distillation sont des
          exemples de transformation.
        </p>

        <p>
          <em>Matière.</em> On appelle matière tout élément atomique pouvant
          subir une transformation. Une plante, un gaz, une roche, un os, une
          peau, une chair et un liquide sont des exemples de matière.
        </p>

        <p>
          <em>Transformation immédiate.</em> On appelle transformation immédiate
          toute transformation ne nécessitant pas d'attendre pour obtenir les
          matières produites. La dilution et le broyage sont des exemples de
          transformation immédiate.
        </p>

        <p>
          <em>Transformation lente.</em> On appelle transformation lente
          toute transformation nécessitant d'attendre pour obtenir les
          matières produites. L'infusion, la décoction, la macération, le
          séchage et la distillation sont des exemples de transformation lente.
        </p>

        <p>
          <em>Élément primitif.</em> On appelle élément primitif toute matière
          extraite directement dans le monde naturel. C'est à dire n'ayant pas
          subie de transformation objet de la volonté d'un alchimiste. Une
          plante, un gaz, un minéral, un os, une peau ou une chair sont des
          exemples d'éléments primitifs.
        </p>

        <p>
          <em>Substrat.</em> On appelle substrat toute matière résultant d'une
          transformation objet de la volonté d'un alchimiste. Une infusion de
          menthe, une décoction de café ou une poudre de germe de blé sont des
          exemples de substrat.
        </p>

        <p>
          <em>Propriété alchimiques.</em> On appelle propriété alchimique d'une
          matière tout ensemble d'effet insécable pouvant être obtenu après
          l'application d'une transformation finale sur celle-ci dite
          transformation de composition. Le résultat d'une transformation de
          composition est toujours un produit alchimique.
        </p>

        <p>
          <em>Transformation de composition.</em> On appelle transformation de
          composition toute transformation activant les propriétés alchimiques
          d'une matière. Le résultat d'une transformation de composition est
          toujours un produit alchimique.
        </p>

        <p>
          <em>Produit alchimique.</em> Une produit alchimique est une matière
          qui peut faire l'objet d'une consommation. Les propriétés alchimiques
          d'un produit alchimique sont actifs et peuvent donc agir sur la cible
          du produit. Un gaz, un poison, un sel, une huile et une potion sont
          des exemples de produit alchimique.
        </p>

        <p>
          L'alchimie c'est d'abord l'art de tranformer la matière. Peut importe
          le type de produit final que l'on cherche à former, qu'il soit potion,
          poison, huile, poudre, sel, crème, crystal ou gaz, il s'agit d'abord
          et avant tout de prendre un ensemble d'éléments et de le transformer
          jusqu'à obtention du produit fini.
        </p>

        <p>
          On appelle matière tout élément que l'on peut transformer. Toute
          matière peut alors appartenir à deux grands ensembles d'objets. On dit
          d'une matière qu'elle est un élément primitif si elle est une plante,
          un gaz, un minéral, un os, une peau, une chair ou tout autre produit
          directement extrait du monde naturel. On dit d'une matière qu'elle est
          un substrat si celle-ci est le résultat d'une transformation.
        </p>

        <p>
          On dit d'un substrat qu'il est une essence si celui-ci ne peut pas
          être transformé
        </p>
      </SubjectContent>
    </Subject>
  )
}
